<template>
 <div id="loadmore">
    <mt-header fixed title="Loadmore使用案例">
      <router-link to="/tool" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
		
		
		<div ref="pageloadmorewrapper" class="page-loadmore-wrapper">
			<mt-loadmore ref="loadmore" :top-method="loadTop" :top-status.sync="topStatus">
				<ul
				v-infinite-scroll="loadMore"
				infinite-scroll-disabled="loading"
				infinite-scroll-distance="40">
					<mt-cell-swipe
						@click.native="clickMe"
						v-for="(item,index) in list"
						:right="[
								{content: '发布',style: {background: 'red', color: '#fff', textAlign: 'center'},handler(){release(index)}},
								{content: '删除',style: {background: 'green', color: '#fff', textAlign: 'center'},handler(){delete1(index)}}
						]"
						:key="index">
							<a class="mui-navigate-right">
								<img class="mui-pull-left" src="../../assets/tool/cbd.jpg">
								<div class="mui-media-body">
									{{item}}
									<p class="mui-ellipsis">烤炉模式的城，到黄昏，如同打翻的调色盘一般.</p>
								</div>
							</a>
					</mt-cell-swipe>
				</ul>
				<p class="page-infinite-loading" v-if="downLoad">
					加载中... <img src="../../assets/tool/loading_s.gif">
				</p>
				<div slot="top" class="mint-loadmore-top">
					<span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'pull' }">下拉加载 <img src="../../assets/tool/loading_s.gif"></span>
					<span v-show="topStatus === 'loading'">Loading...</span>
				</div>
			</mt-loadmore>
		</div>
		
  </div>
</template>
<style lang="scss">
	#loadmore {
		position: absolute;
		top: 30px;
		left: 0;
		height: 100%;
		width: 100%;
	}
	.page-loadmore-wrapper {
		margin-top: 10px;
	}
	.page-loadmore-wrapper ul {
		margin: 0;
		padding: 0;
	}
	.mint-header.is-fixed {
		background-color: #cd2525;
	}
	.mui-navigate-right {
		
	}
	.mui-navigate-right .mui-pull-left {
		line-height: 50px;
    max-width: 50px;
    height: 50px;
	}
	.mint-cell-swipe-button {
		line-height: 60px;
	}
	.mint-cell-value {
		display: flex;
	}
	.mui-navigate-right .mui-pull-left {
		float: left;
		margin-right: 10px;
	}
	.mui-navigate-right .mui-media-body {
		fa-pulse: 1;
		margin-top: 5px;
	}
	.mui-navigate-right .mui-media-body .mui-ellipsis {
		margin: 0;
		margin-top: 8px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.mint-cell-title {
		display: none;
	}
	.mint-cell-wrapper {
		border: none;
		border-bottom: 1px solid #ccc;
		padding: 6px 10px;
	}
	.mint-loadmore-bottom {
		height: 40px;
		margin-bottom: -40px;
		line-height: 40px;
	}
	.mint-loadmore-content {
		transform: translate3d(0px, 0px, 0px);
	}
	.page-infinite-loading {
		text-align: center;
		margin-top: 10px;
	}
</style>
<script>
  import {Toast} from 'mint-ui'
// class="mui-table-view mui-table-view-chevron"
  export default {
    data(){
      return {
        bottomAllLoaded: false,
				downLoad: true,
        topStatus: '',
        bottomStatus:'',
				translate: 0,
        list: []
      }
    },
    methods: {
      release(val){
        console.info('release:' + val)
      },
      delete1(val){
        console.info('delete:' + val)
      },
      clickMe(){
        console.info('click me')
      },
      loadTop(){
        let that = this;
        for (let i = 0; i < 2; i++) {
          this.list.unshift('unshift' + i)
        }
        setTimeout(function () {
          that.$refs.loadmore.onTopLoaded();
        }, 500)

      },
      loadBottom(){
        for (let i = 0; i < 2; i++) {
          this.list.push('push' + i)
        }
        if (this.list.length > 100) {
          this.bottomAllLoaded = true;
        }
        this.$refs.loadmore.onBottomLoaded();
      },
			loadMore() {
				this.loading = true;
				setTimeout(() => {
					let last = this.list[this.list.length - 1];
					for (let i = 1; i <= 3; i++) {
						this.list.push(last + i);
					}
					this.loading = false;
				}, 1500);
			},
      handleTopChange(status){
        this.topStatus = status;
      },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
    },
		computed: {
		  transform() {
		    return this.translate === 0 ? null : 'translate3d(0, ' + this.translate + 'px, 0)';
		  }
		},
		created(){
		  for (let i = 0; i < 10; i++) {
		    this.list.push(i)
		  }
		},
    mounted(){
			var innh = window.innerHeight-40;
			this.$refs.pageloadmorewrapper.style.height = innh+"px"
    }
  }
</script>



